import React from 'react';
import CardGrid from './../../CardGrid/CardGrid';
import { connect } from 'react-redux';
import { getModels, deleteModel } from './../../../actions';

class DeleteModel extends React.Component {
  componentDidMount() {
    this.props.getModels();
  }

  onDeleteButtonClick = (model) => {
    this.props.deleteModel(model._id);
  };

  render() {
    return (
      <div>
        <CardGrid
          models={this.props.models}
          onDeleteButtonClick={this.onDeleteButtonClick}
        />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return { models: state.model.models };
};

export default connect(mapStateToProps, { getModels, deleteModel })(
  DeleteModel
);
