import React from 'react';
import EditGrid from './EditGrid';
import EditForm from './EditForm';
import { connect } from 'react-redux';
import { getModels, selectModel } from './../../../actions';

class EditModel extends React.Component {
  componentDidMount() {
    this.props.getModels();
  }

  renderAdmin = () => {
    if (!this.props.selectedModel) {
      return (
        <>
          <EditGrid
            selectModel={this.props.selectModel}
            models={this.props.models}
          />
        </>
      );
    } else {
      return (
        <>
          <EditForm selectedModel={this.props.selectedModel} />
        </>
      );
    }
  };

  render() {
    return this.renderAdmin();
  }
}

const mapStateToProps = (state) => {
  return {
    models: state.model.models,
    selectedModel: state.model.selectedModel,
  };
};

export default connect(mapStateToProps, { getModels, selectModel })(EditModel);
