import React from 'react';
import AdminForm from './../AdminForm/AdminForm';
import { connect } from 'react-redux';
import { reduxForm, change } from 'redux-form';
import {
  editModel,
  imagesToUpload,
  imagesToSubmit,
  filesToUpload,
  filesToSubmit,
} from './../../../actions/index';

const formProps = {
  type: 'Edit Model',
  label1: 'Model Name: ',
  label2: 'Model Description: ',
  label3: 'Model Classification: ',
  label4: 'Model Image(s): ',
  label5: 'Model Price: ',
  label6: 'Model Formats: ',
  label7: 'Model Files: ',
  button1: 'Change Model',
};

class EditForm extends React.Component {
  componentDidMount = () => {
    this.props.dispatch(
      change('modelEdit', 'modelName', this.props.selectedModel.modelName)
    );
    this.props.dispatch(
      change(
        'modelEdit',
        'modelDescription',
        this.props.selectedModel.modelDescription
      )
    );
    this.props.dispatch(
      change('modelEdit', 'modelPrice', this.props.selectedModel.modelPrice)
    );
    document.querySelector(
      '#classification-selectbox'
    ).value = this.props.selectedModel.modelClassification;
    for (let i = 0; i < this.props.selectedModel.modelImages.length; i++) {
      document.querySelector(
        `#image-preview-${i}`
      ).src = this.props.selectedModel.modelImages[i];
    }
    const check = (value) => {
      switch (value) {
        case 'stl':
          return (document.querySelector(`#format-checkbox0`).checked = true);
        case 'obj':
          return (document.querySelector(`#format-checkbox1`).checked = true);
        case 'mtl':
          return (document.querySelector(`#format-checkbox2`).checked = true);
        default:
          return null;
      }
    };
    for (let i = 0; i < this.props.selectedModel.modelFormats.length; i++) {
      check(this.props.selectedModel.modelFormats[i]);
    }
  };

  renderInput = ({ input }) => {
    switch (input.name) {
      case 'modelName':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label1}</label>
            <input placeholder="Name of the Model" {...input} />
          </div>
        );
      case 'modelDescription':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label2}</label>
            <textarea
              placeholder="Brief description of the Model"
              style={{ resize: 'none' }}
              {...input}
            />
          </div>
        );
      case 'modelClassification':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label3}</label>
            <select
              className="ui fluid dropdown"
              placeholder="Select Classification"
              {...input}
              id="classification-selectbox"
            >
              <option value="">Select Classification</option>
              <option value="Anime/Manga">Anime/Manga</option>
              <option value="Movies">Movies</option>
              <option value="Videogames">Videogames</option>
            </select>
          </div>
        );
      case 'modelImages':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label4}</label>
            <div className="image-preview-container">
              <img
                className="image-preview-container-image"
                id="image-preview-0"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-1"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-2"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-3"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-4"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-5"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-6"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-7"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-8"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-9"
                src=""
                alt=""
              />
            </div>
            <input
              type="file"
              multiple="multiple"
              onChange={this.imageSelectedHandler}
              name={input.name}
              onBlur={input.onBlur}
              onDragStart={input.onDragStart}
              onDrop={input.onDrop}
              onFocus={input.onFocus}
            />
          </div>
        );
      case 'modelFiles':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label7}</label>
            <input
              type="file"
              onChange={this.fileSelectedHandler}
              name={input.name}
              onBlur={input.onBlur}
              onDragStart={input.onDragStart}
              onDrop={input.onDrop}
              onFocus={input.onFocus}
            />
          </div>
        );
      case 'modelPrice':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label5}</label>
            <input placeholder="$" {...input} />
          </div>
        );
      case 'modelFormats':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label6}</label>
            <div className="addmodel-checkbox-container">
              <div className="addmodel-checkbox-container-option">
                <label htmlFor="stl" id="format-checkbox0-label">
                  stl
                </label>
                <input type="checkbox" id="format-checkbox0" />
              </div>
              <div className="addmodel-checkbox-container-option">
                <label htmlFor="obj" id="format-checkbox1-label">
                  obj
                </label>
                <input type="checkbox" id="format-checkbox1" />
              </div>
              <div className="addmodel-checkbox-container-option">
                <label htmlFor="mtl" id="format-checkbox2-label">
                  mtl
                </label>
                <input type="checkbox" id="format-checkbox2" />
              </div>
            </div>
            <input style={{ display: 'none' }} {...input} />
          </div>
        );
      default:
        return null;
    }
  };

  imageSelectedHandler = (event) => {
    for (let i = 0; i < 10; i++) {
      document.querySelector(`#image-preview-${i}`).src = '';
    }
    let base64Arr = [];
    const inner = (file, i) => {
      const reader = new FileReader();

      reader.addEventListener(
        'load',
        () => {
          base64Arr.push(reader.result);
          document.querySelector(`#image-preview-${i}`).src = reader.result;
        },
        false
      );

      if (file) {
        reader.readAsDataURL(file);
      }
    };
    if (event.target.files.length > 1) {
      for (let i = 0; i < event.target.files.length; i++) {
        inner(event.target.files[i], i);
      }
    } else {
      inner(event.target.files[0], 0);
    }
    this.props.imagesToSubmit(base64Arr);
  };

  fileSelectedHandler = (event) => {
    let base64Arr = [];
    const inner = (file) => {
      const reader = new FileReader();

      reader.addEventListener(
        'load',
        () => {
          base64Arr.push(reader.result);
        },
        false
      );

      if (file) {
        reader.readAsDataURL(file);
      }
    };
    inner(event.target.files[0]);
    this.props.filesToSubmit(base64Arr);
  };

  onSubmit = (formValues) => {
    console.log(this.props);
    let formatArr = [];
    for (let i = 0; i < 2; i++) {
      if (document.querySelector(`#format-checkbox${i}`).checked)
        formatArr.push(
          document.querySelector(`#format-checkbox${i}-label`).innerHTML
        );
    }
    this.props.editModel(
      formValues,
      document.querySelector('#classification-selectbox').value,
      formatArr,
      this.props.imagesSubmit,
      this.props.filesSubmit,
      this.props.selectedModel._id
    );
    this.props.dispatch(change('modelCreate', 'modelName', ''));
    this.props.dispatch(change('modelCreate', 'modelDescription', ''));
    this.props.dispatch(change('modelCreate', 'modelPrice', ''));
    for (let i = 0; i < 9; i++) {
      document.querySelector(`#image-preview-${i}`).src = '';
    }
  };

  render() {
    console.log(this.props.selectedModel);
    return (
      <React.Fragment>
        <div
          className="form-type-title"
          onSubmit={this.props.handleSubmit(this.onSubmit)}
        >
          <h2>{formProps.type}</h2>
        </div>
        <form
          className="ui form addmodel-container"
          onSubmit={this.props.handleSubmit(this.onSubmit)}
        >
          <AdminForm formProps={formProps} renderInput={this.renderInput} />
        </form>
      </React.Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    images: state.model.images,
    files: state.model.files,
    imagesSubmit: state.model.imagesSubmit,
    filesSubmit: state.model.filesSubmit,
  };
};

export default connect(mapStateToProps, {
  editModel,
  imagesToUpload,
  imagesToSubmit,
  filesToUpload,
  filesToSubmit,
})(reduxForm({ form: 'modelEdit' })(EditForm));
