import './Account.css';
import React from 'react';
import google from './../../../media/images/google.png';
import { connect } from 'react-redux';
import history from './../../../history';
import {
  checkIfUserIsRegistered,
  changeAccountButtonState,
  checkLoggedState,
} from './../../../actions';

class Account extends React.Component {
  componentDidUpdate() {
    if (this.props.user.isUserLoggedIn) {
      document.querySelector('#signin-button').innerHTML = 'Log Out';
    }

    if (!this.props.user.isUserLoggedIn) {
      document.querySelector('#signin-button').innerHTML = 'Log In / Register';
      document.querySelectorAll('.account-dropdown-items')[
        document.querySelectorAll('.account-dropdown-items').length - 1
      ].style.visibility = 'hidden';
    }
  }

  handleAuth() {
    const { isSignedIn, signIn, signOut } = window.gapi.auth2.getAuthInstance();

    if (!isSignedIn.le) {
      signIn()
        .then((res) => {
          this.props.checkIfUserIsRegistered(res.nt);
        })
        .then(() => {
          this.props.checkLoggedState();
        })
        .then(() => {
          history.go(`${window.location.protocol}//${window.location.host}`);
        });
    } else {
      signOut()
        .then(() => {
          this.props.checkLoggedState();
        })
        .then(() => {
          history.go(`${window.location.protocol}//${window.location.host}`);
        });
    }
    document.querySelector('#account-dropdown-menu').className =
      'account-dropdown-menu-inactive';
    document.querySelectorAll('.account-dropdown-items').forEach((el) => {
      el.style.visibility = 'hidden';
    });
    this.props.changeAccountButtonState(true);
  }

  render() {
    return (
      <ul className="account-dropdown-menu-inactive" id="account-dropdown-menu">
        <li className="account-dropdown-items" style={{ padding: '10px' }}>
          <div
            style={{
              border: '1px solid white',
              borderRadius: '5px',
              background: 'white',
              display: 'flex',
              justifyContent: 'center',
              padding: '5px',
              color: 'black',
            }}
            onClick={() => {
              this.handleAuth();
            }}
          >
            <div
              style={{
                height: '20px',
                width: '20px',
              }}
            >
              <img src={google} alt="" />
            </div>
            <div style={{ paddingLeft: '10px', paddingRight: '15px' }}>
              <p id="signin-button"></p>
            </div>
          </div>
        </li>
        <li
          className="account-dropdown-items"
          onClick={() => {
            this.props.changeAccountButtonState(true);
            window.open(
              `${window.location.protocol}//${window.location.host}/MyCollection`,
              '_self'
            );
          }}
        >
          My Collection
        </li>
      </ul>
    );
  }
}

const mapStateToProps = (state) => {
  return { user: state.user, header: state.header };
};

export default connect(mapStateToProps, {
  checkIfUserIsRegistered,
  changeAccountButtonState,
  checkLoggedState,
})(Account);
