import './Header.css';
import React from 'react';
import Account from './../Shop/Account/Account';
import UseAnimations from 'react-useanimations';
import menu2 from 'react-useanimations/lib/menu2';
import { connect } from 'react-redux';
import {
  changeAccountButtonState,
  checkLoggedState,
  getModels,
} from './../../actions';

class Header extends React.Component {
  componentDidMount() {
    if (
      `${window.location.href}` !==
      `${window.location.protocol}//${window.location.host}/`
    ) {
      document.getElementById('menu-bar').style.display = 'none';
      document.getElementById('header-mobile-dropdown').style.display = 'none';
    }
  }

  componentDidUpdate() {
    if (this.props.header.accountButtonState) {
      document.querySelector('#account-dropdown-menu').className =
        'account-dropdown-menu';
      document.querySelectorAll('.account-dropdown-items').forEach((el, i) => {
        if (i !== 1) {
          el.style.visibility = 'visible';
        } else if (this.props.user.isUserLoggedIn) {
          el.style.visibility = 'visible';
        }
      });
    } else {
      document.querySelector('#account-dropdown-menu').className =
        'account-dropdown-menu-inactive';
      document.querySelectorAll('.account-dropdown-items').forEach((el) => {
        el.style.visibility = 'hidden';
      });
    }
  }

  onDropDownMenuIconClick() {
    const dropDown = document.querySelector('#menu-bar');
    const menuItems = document.querySelectorAll('.menu-item');
    const menuItemsI = document.querySelectorAll('.menu-item-inactive');

    if (
      dropDown.className === '' ||
      dropDown.className === 'dropdown-mobile-menu-inactive'
    ) {
      dropDown.className = 'dropdown-mobile-menu-active';
      menuItemsI.forEach((element) => {
        element.className = 'menu-item';
      });
    } else if ((dropDown.className = 'dropdown-mobile-menu-active')) {
      dropDown.className = 'dropdown-mobile-menu-inactive';
      menuItems.forEach((element) => {
        element.className = 'menu-item-inactive';
      });
    }
  }

  render() {
    return (
      <div className="header-wrapper">
        <div className="pure-menu pure-menu-horizontal headerr">
          <div className="header-logo">
            <UseAnimations
              style={{ color: 'white' }}
              animation={menu2}
              id="header-mobile-dropdown"
              className="header-mobile-dropdown"
              onClick={() => this.onDropDownMenuIconClick()}
            />
            <div
              className="header-logo-title"
              onClick={() =>
                window.open(
                  `${window.location.protocol}//${window.location.host}`,
                  '_self'
                )
              }
            >
              <p>HushPuppy's Hub</p>
            </div>
          </div>
          <div className="header-right-options">
            {this.props.user.isUserLoggedIn && this.props.models ? (
              <div className="header-right-options-items">
                <i
                  className="shopping cart icon"
                  onClick={() => {
                    if (!this.props.cart.cart) {
                      this.props.showCartModal();
                    } else {
                      window.open(
                        `${window.location.protocol}//${window.location.host}/Cart`,
                        '_self'
                      );
                    }
                  }}
                />
              </div>
            ) : null}
            <div className="header-right-options-items">
              <i
                className="user circle icon"
                onClick={() => {
                  this.props.checkLoggedState();
                  !this.props.header.accountButtonState
                    ? this.props.changeAccountButtonState()
                    : this.props.changeAccountButtonState(
                        this.props.header.accountButtonState
                      );
                }}
              />
            </div>
          </div>
        </div>
        <div style={{ height: '100%', width: '200px' }}>
          <ul id="menu-bar" className="dropdown-mobile-menu-inactive">
            <li
              className="menu-item-inactive"
              onClick={() => this.props.getModels()}
            >
              All Models
            </li>
            <li
              className="menu-item-inactive"
              onClick={() => this.props.getModels('Anime-Manga')}
            >
              Anime/Manga
            </li>
            <li
              className="menu-item-inactive"
              onClick={() => this.props.getModels('Movies')}
            >
              Movies
            </li>
            <li
              className="menu-item-inactive"
              onClick={() => this.props.getModels('Videogames')}
            >
              Videogames
            </li>
          </ul>
        </div>
        <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
          <Account />
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    header: state.header,
    user: state.user,
    cart: state.cart,
    models: state.model.models,
  };
};

export default connect(mapStateToProps, {
  changeAccountButtonState,
  checkLoggedState,
  getModels,
})(Header);
