import './AddModel.css';
import React from 'react';
import AdminForm from '../AdminForm/AdminForm';
import { connect } from 'react-redux';
import { reduxForm, change } from 'redux-form';
import axios from 'axios';
import {
  createModel,
  imagesToUpload,
  imagesToSubmit,
  filesToUpload,
  filesToSubmit,
} from './../../../actions/index';
// import history from './../../../history';

const formProps = {
  type: 'Add Model',
  label1: 'Model Name: ',
  label2: 'Model Description: ',
  label3: 'Model Classification: ',
  label4: 'Model Image(s): ',
  label5: 'Model Price: ',
  label6: 'Model Formats: ',
  label7: 'Model Files: ',
  button1: 'Create Model',
};

class AddModel extends React.Component {
  renderInput = ({ input }) => {
    switch (input.name) {
      case 'modelName':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label1}</label>
            <input placeholder="Name of the Model" {...input} />
          </div>
        );
      case 'modelDescription':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label2}</label>
            <textarea
              placeholder="Brief description of the Model"
              style={{ resize: 'none' }}
              {...input}
            />
          </div>
        );
      case 'modelClassification':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label3}</label>
            <select
              className="ui fluid dropdown"
              placeholder="Select Classification"
              {...input}
              id="classification-selectbox"
            >
              <option value="">Select Classification</option>
              <option value="Anime/Manga">Anime/Manga</option>
              <option value="Movies">Movies</option>
              <option value="Videogames">Videogames</option>
            </select>
          </div>
        );
      case 'modelImages':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label4}</label>
            <div className="image-preview-container">
              <img
                className="image-preview-container-image"
                id="image-preview-0"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-1"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-2"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-3"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-4"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-5"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-6"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-7"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-8"
                src=""
                alt=""
              />
              <img
                className="image-preview-container-image"
                id="image-preview-9"
                src=""
                alt=""
              />
            </div>
            <input
              type="file"
              multiple="multiple"
              onChange={this.imageSelectedHandler}
              name={input.name}
              onBlur={input.onBlur}
              onDragStart={input.onDragStart}
              onDrop={input.onDrop}
              onFocus={input.onFocus}
            />
          </div>
        );
      case 'modelFiles':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label7}</label>
            <input
              type="file"
              onChange={this.fileSelectedHandler}
              name={input.name}
              onBlur={input.onBlur}
              onDragStart={input.onDragStart}
              onDrop={input.onDrop}
              onFocus={input.onFocus}
            />
          </div>
        );
      case 'modelPrice':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label5}</label>
            <input placeholder="$" {...input} />
          </div>
        );
      case 'modelFormats':
        return (
          <div className="field">
            <label className="addmodel-field-label">{formProps.label6}</label>
            <div className="addmodel-checkbox-container">
              <div className="addmodel-checkbox-container-option">
                <label htmlFor="stl" id="format-checkbox0-label">
                  stl
                </label>
                <input type="checkbox" id="format-checkbox0" />
              </div>
              <div className="addmodel-checkbox-container-option">
                <label htmlFor="obj" id="format-checkbox1-label">
                  obj
                </label>
                <input type="checkbox" id="format-checkbox1" />
              </div>
              <div className="addmodel-checkbox-container-option">
                <label htmlFor="mtl" id="format-checkbox2-label">
                  mtl
                </label>
                <input type="checkbox" id="format-checkbox2" />
              </div>
            </div>
            <input style={{ display: 'none' }} {...input} />
          </div>
        );
      default:
        return null;
    }
  };

  imageSelectedHandler = async (event) => {
    for (let i = 0; i < 10; i++) {
      document.querySelector(`#image-preview-${i}`).src = '';
    }
    let base64Arr = [];
    const inner = (file, i) => {
      const reader = new FileReader();

      reader.addEventListener(
        'load',
        () => {
          base64Arr.push(reader.result);
          document.querySelector(`#image-preview-${i}`).src = reader.result;
        },
        false
      );

      if (file) {
        reader.readAsDataURL(file);
      }
    };
    if (event.target.files.length > 1) {
      for (let i = 0; i < event.target.files.length; i++) {
        inner(event.target.files[i], i);
      }
    } else {
      inner(event.target.files[0], 0);
    }
    this.props.imagesToSubmit(base64Arr);
  };

  fileSelectedHandler = (event) => {
    this.props.filesToSubmit(event.target.files[0]);
  };

  showUploadModal = () => {
    document.querySelector('#upload-modal').style.display = 'flex';
  };

  onSubmit = async (formValues) => {
    console.log(this.props);
    let formatArr = [];
    for (let i = 0; i < 2; i++) {
      if (document.querySelector(`#format-checkbox${i}`).checked)
        formatArr.push(
          document.querySelector(`#format-checkbox${i}-label`).innerHTML
        );
    }
    this.showUploadModal();
    await this.props.createModel(
      formValues,
      document.querySelector('#classification-selectbox').value,
      formatArr,
      this.props.imagesSubmit
    );
    this.props.dispatch(change('modelCreate', 'modelName', ''));
    this.props.dispatch(change('modelCreate', 'modelDescription', ''));
    this.props.dispatch(change('modelCreate', 'modelPrice', ''));
    for (let i = 0; i < 9; i++) {
      document.querySelector(`#image-preview-${i}`).src = '';
    }
    console.log(this.props.filesSubmit);
    let fileExt = '';
    let afterdot = false;
    for (let i = 0; i < this.props.filesSubmit.name.length; i++) {
      if (afterdot) {
        fileExt = fileExt + this.props.filesSubmit.name[i];
      }
      if (this.props.filesSubmit.name[i] === '.') {
        afterdot = true;
      }
    }
    const data = new FormData();
    data.append('file', this.props.filesSubmit);
    data.append('fileName', `${this.props.filename}.${fileExt}`);
    for (var pair of data.entries()) {
      console.log(pair[0] + ', ' + pair[1]);
    }
    // 'https://hushpuppys-3d-hub-api.herokuapp.com/api/v1/modelfiles/'
    const config = {
      onUploadProgress: function (progressEvent) {
        var percentCompleted = Math.round(
          (progressEvent.loaded * 100) / progressEvent.total
        );
        document.querySelector(
          '#modal-upload-progress'
        ).innerHTML = `${percentCompleted} %`;
        document.querySelector(
          '#upload-bar'
        ).style.width = `${percentCompleted}%`;
        document
          .querySelector('#upload-bar-indicator')
          .setAttribute('data-percent', `${percentCompleted}`);
      },
      headers: {
        'content-type': 'multipart/form-data',
      },
    };
    await axios
      .post(
        'https://hushpuppys-3d-hub-api.herokuapp.com/api/v1/modelfiles/',
        data,
        config
      )
      .then((res) => {
        console.log('File Upload Successful! Res: ', res);
        document.querySelector('#upload-modal').style.display = 'none';
      })
      .catch((err) => {
        console.log(err);
      });
  };

  render() {
    return (
      <React.Fragment>
        <div
          className="form-type-title"
          onSubmit={this.props.handleSubmit(this.onSubmit)}
        >
          <h2>{formProps.type}</h2>
        </div>
        <form
          className="ui form addmodel-container"
          onSubmit={this.props.handleSubmit(this.onSubmit)}
        >
          <AdminForm formProps={formProps} renderInput={this.renderInput} />
        </form>
        <div id="upload-modal" className="upload-modal">
          <div className="upload-modal-content">
            <p className="upload-modal-header">
              <i
                className="exclamation circle icon"
                style={{ color: 'gray' }}
              />
              Uploading Files To Cloud Storage...
            </p>
            <div
              className="ui indicating progress active progress-indicator"
              id="upload-bar-indicator"
            >
              <div className="bar" id="upload-bar">
                <div className="progress" id="modal-upload-progress"></div>
              </div>
              <div className="label">Uploaded</div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    filename: state.model.filename,
    images: state.model.images,
    files: state.model.files,
    imagesSubmit: state.model.imagesSubmit,
    filesSubmit: state.model.filesSubmit,
  };
};

export default connect(mapStateToProps, {
  createModel,
  imagesToUpload,
  imagesToSubmit,
  filesToUpload,
  filesToSubmit,
})(reduxForm({ form: 'modelCreate' })(AddModel));
