import './Gallery.css';
import React from 'react';
import Header from '../Header/Header';
import Footer from '../Footer/Footer';
import CardGrid from './../CardGrid/CardGrid';
import { connect } from 'react-redux';
import {
  getModels,
  selectedPreviewModel,
  checkIfAcquired,
} from './../../actions';
import logImage from './../../media/images/logImage.png';

class Gallery extends React.Component {
  componentDidMount = async () => {
    this.props.getModels();
    if (this.props.user) {
      this.props.checkIfAcquired(this.props.user.gId);
    }
  };

  showLogModal = () => {
    document.querySelector('#log-modal').style.display = 'flex';
  };

  showCartModal = () => {
    document.querySelector('#cart-modal').style.display = 'flex';
  };

  render() {
    return (
      <div className="gallery-wrapper">
        <div className="gallery-container">
          <Header showCartModal={this.showCartModal} />
          <div id="log-modal" className="log-modal">
            <div className="log-modal-content">
              <p className="log-modal-header">
                <i
                  className="exclamation circle icon"
                  style={{ color: 'gray' }}
                />
                Before Adding items to your cart you have to log in:
              </p>
              <div>
                <img src={logImage} className="log-modal-image" alt="" />
              </div>
              <div className="log-modal-button">
                <div
                  className="ui green button"
                  onClick={() => {
                    document.querySelector('#log-modal').style.display = 'none';
                  }}
                >
                  Okay
                </div>
              </div>
            </div>
          </div>
          <div id="cart-modal" className="cart-modal">
            <div className="cart-modal-content">
              <p className="cart-modal-header">
                <i
                  className="exclamation circle icon"
                  style={{ color: 'gray' }}
                />
                Before Accessing to the cart you have to add items.
              </p>
              <div className="cart-modal-button">
                <div
                  className="ui green button"
                  onClick={() => {
                    document.querySelector('#cart-modal').style.display =
                      'none';
                  }}
                >
                  Okay
                </div>
              </div>
            </div>
          </div>
          <CardGrid
            selectedPreviewModel={this.props.selectedPreviewModel}
            models={this.props.models}
            cart={this.props.cart}
            acquiredModels={this.props.acquiredModels}
            showLogModal={this.showLogModal}
          />
        </div>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    models: state.model.models,
    preview: state.model.selectedPreviewModel,
    cart: state.cart.cart,
    user: state.user.userData,
    acquiredModels: state.cart.acquiredModels,
  };
};

export default connect(mapStateToProps, {
  getModels,
  selectedPreviewModel,
  checkIfAcquired,
})(Gallery);
