export default (state = {}, action) => {
  switch (action.type) {
    case 'USER_CHECK':
      return {
        ...state,
        isUserLoggedIn: action.payload.status,
        userData: action.payload.userData,
      };

    case 'CHECK_LOG_STATE':
      return {
        ...state,
        isUserLoggedIn: action.payload.status,
        userData: action.payload.userData,
      };

    default:
      return state;
  }
};
