import './CardGrid.css';
import React from 'react';
import _ from 'lodash';
import { connect } from 'react-redux';
import { addToCart } from './../../actions';

class CardGrid extends React.Component {
  renderAddToCartButton = (element) => {
    let exist;
    let acquired;
    let acquiredDone = false;

    if (this.props.cart) {
      for (let i = 0; i < this.props.cart.length; i++) {
        if (_.isEqual(this.props.cart[i], element)) {
          exist = true;
        }
        if (exist !== true && i === this.props.cart.length - 1) {
          exist = false;
        }
      }
    }

    if (this.props.acquiredModels) {
      if (
        !this.props.acquiredModels.acquiredModels.find(
          (el) => el === element._id
        ) &&
        !acquiredDone
      ) {
        acquired = false;
      } else if (
        this.props.acquiredModels.acquiredModels.find(
          (el) => el === element._id
        ) &&
        !acquiredDone
      ) {
        acquired = true;
        acquiredDone = true;
      }
    }

    if (exist && !acquired) {
      return (
        <div className="ui button card-buttons">
          <p className="button-inner-text"></p>
        </div>
      );
    } else if (!exist && !acquired) {
      return (
        <div
          className="ui button card-buttons"
          onClick={() => {
            if (!window.gapi.auth2.getAuthInstance().isSignedIn.le) {
              this.props.showLogModal();
            } else {
              if (!this.props.cart) {
                this.props.addToCart(element);
              } else if (this.props.cart) {
                this.props.addToCart(element, this.props.cart);
              }
            }
          }}
        >
          <p className="button-inner-text">Add to cart</p>
        </div>
      );
    } else if (acquired) {
      return (
        <div className="ui button card-buttons" id="card-acquired-button">
          <p className="button-inner-text">Acquired</p>
        </div>
      );
    }
  };

  buttonRenderAdmin = (path, element) => {
    if (path === '/') {
      return (
        <div className="card-grid-button-container-gallery">
          <div
            className="ui button card-buttons"
            onClick={() => {
              this.props.selectedPreviewModel(element.modelPreview);
              window.open(
                `${window.location.origin}/ModelViewer/${element._id}`,
                '_blank',
                'channelmode=yes,height=500,width=500,top=100,left=500,resizeable=no,menubar=no,titlebar=no'
              );
            }}
          >
            <p className="button-inner-text">Preview</p>
          </div>
          {this.renderAddToCartButton(element)}
        </div>
      );
    } else if (path === '/admin/edit-model') {
      return (
        <div className="card-grid-button-container-singlebutton">
          <div
            className="ui button card-buttons"
            onClick={() => this.props.onEditButtonClick(element)}
          >
            <p className="button-inner-text">Edit</p>
          </div>
        </div>
      );
    } else if (path === '/admin/delete-model') {
      return (
        <div className="card-grid-button-container-singlebutton">
          <div
            className="ui button card-buttons"
            onClick={() => this.props.onDeleteButtonClick(element)}
          >
            <p className="button-inner-text">Delete</p>
          </div>
        </div>
      );
    }
  };

  cardRender = () => {
    return this.props.models.map((element, index) => {
      return (
        <div className="column card cardgrid-card" key={index}>
          <div className="image cardgrid-card-image-container">
            <img src={element.modelImages[0]} alt="" />
          </div>
          <div content="content">
            <h4 className="ui header card-header">{element.modelName}</h4>
            <div className="meta">
              <strong>Formats: </strong>
              {element.modelFormats.map((el, i) => {
                if (i < element.modelFormats.length - 1) {
                  return `${el}, `;
                } else {
                  return `${el}.`;
                }
              })}
            </div>
            <div className="description">
              <div className="card-pricetag">
                <h3>
                  <strong>$</strong>
                  {element.modelPrice}
                </h3>
              </div>
              {this.buttonRenderAdmin(window.location.pathname, element)}
            </div>
          </div>
        </div>
      );
    });
  };

  render() {
    if (!this.props.models) {
      return (
        <div className="ui active inverted dimmer listing-lazy-load">
          <div className="ui text loader">Loading Listing...</div>
        </div>
      );
    }
    return (
      <div className="ui stackable three column grid cards gridcard-card-container">
        {this.cardRender()}
      </div>
    );
  }
}

export default connect(null, { addToCart })(CardGrid);
