import axios from 'axios';
import history from './../history';
import models from './../apis/models';
import users from './../apis/users';
import collections from './../apis/collections';

export const createModel = (formValues, classification, formatArr, images) => {
  return async (dispatch) => {
    formValues.modelPrice = parseInt(formValues.modelPrice);
    formValues = {
      ...formValues,
      modelClassification: classification,
      modelFormats: formatArr,
      modelImages: images,
    };

    const response = await models.post('/', formValues);
    console.log('POST REQUEST SUCCESSFULL');
    console.log(response);

    dispatch({
      type: 'CREATE_MODEL',
      payload: response.data.data.newModel._id,
    });
    // history.go('/admin');
  };
};

export const editModel = (
  formValues,
  classification,
  formatArr,
  images,
  files,
  id
) => {
  return async (dispatch) => {
    formValues.modelPrice = parseInt(formValues.modelPrice);

    if (!files) {
      formValues = {
        ...formValues,
        modelClassification: classification,
        modelFormats: formatArr,
        modelImages: images,
        _id: id,
      };
    } else {
      formValues = {
        ...formValues,
        modelClassification: classification,
        modelFormats: formatArr,
        modelImages: images,
        modelPreview: files[0],
        _id: id,
      };
    }

    const response = await models.patch(`/${id}`, formValues);

    dispatch({ type: 'CREATE_MODEL', payload: response.data });
    history.go('/admin');
  };
};

