export const getModels = (classif) => {
  // http://localhost:8000/api/v1/model
  // https://hushpuppys-3d-hub-api.herokuapp.com/api/v1/models
  return async (dispatch) => {
    console.log(classif);
    if (!classif) {
      const response = await models.get('/');
      dispatch({ type: 'GET_MODELS', payload: response.data.data });
    } else {
      const response = await models.get(`/ByClassification/${classif}`);
      dispatch({ type: 'GET_MODELS', payload: response.data.data });
    }
  };
};

export const getModel = (id) => {
  return async (dispatch) => {
    // https://hushpuppys-3d-hub-api.herokuapp.com/api/v1/modelfiles/${id}.obj
    // http://localhost:8000/api/v1/modelfiles/${id}.obj
    const response = await axios.get(
      `http://localhost:8000/api/v1/modelfiles/${id}.obj`
    );
    document.querySelector('#upload-modal').style.display = 'none';

    dispatch({ type: 'GET_MODEL', payload: response.data.url });
  };
};

export const selectModel = (model) => {
  return async (dispatch) => {
    dispatch({
      type: 'SELECTED_MODEL',
      payload: model,
    });
  };
};

export const selectedPreviewModel = (model) => {
  return async (dispatch) => {
    dispatch({ type: 'SELECTED_PREVIEW_MODEL', payload: model });
  };
};

export const deleteModel = (id) => {
  return async (dispatch) => {
    const response = await models.delete(`/${id}`);

    dispatch({ type: 'DELETE_MODEL', payload: response.data });
    history.go('/admin');
  };
};

export const changeAccountButtonState = (state) => {
  return async (dispatch) => {
    if (!state) {
      state = true;
    } else {
      state = false;
    }
    dispatch({ type: 'CHANGE_ACCOUNT_BUTTON_STATE', payload: state });
  };
};

export const checkIfUserIsRegistered = (userData) => {
  return async (dispatch) => {
    const response = await users.get(`/${userData.yT}`);

    if (!response.data.regStatus) {
      await users.post('/', userData);
    }

    dispatch({
      type: 'USER_CHECK',
      payload: {
        status: true,
        userData: { gId: userData.yT, name: userData.dV },
      },
    });
  };
};

export const checkLoggedState = () => {
  return async (dispatch) => {
    if (window.gapi.auth2.getAuthInstance().isSignedIn.le) {
      dispatch({
        type: 'CHECK_LOG_STATE',
        payload: {
          status: true,
          userData: {
            gId: window.gapi.auth2.getAuthInstance().currentUser.le.nt.yT,
            name: window.gapi.auth2.getAuthInstance().currentUser.le.nt.dV,
          },
        },
      });
    } else {
      dispatch({
        type: 'CHECK_LOG_STATE',
        payload: {
          status: false,
          userData: null,
        },
      });
    }
  };
};

export const addToCart = (item, cart) => {
  return async (dispatch) => {
    if (!cart) {
      dispatch({ type: 'ADD_TO_CART_NEW', payload: item });
    } else if (cart) {
      dispatch({
        type: 'ADD_TO_CART_EXISTENT',
        payload: { cart: cart, item: item },
      });
    }
  };
};

export const deleteCartItem = (item, cart) => {
  return async (dispatch) => {
    dispatch({ type: 'DELETE_CART_ITEM', payload: { cart: cart, item: item } });
  };
};

export const checkIfAcquired = (gId) => {
  return async (dispatch) => {
    const response = await collections.get(`/${gId}`);

    dispatch({ type: 'CHECK_IF_ACQUIRED', payload: response.data.data });
  };
};

export const getAcquiredModels = (ids) => {
  return async (dispatch) => {
    const response = await collections.get(`/getmodels/${ids}`);

    dispatch({ type: 'GET_ACQUIRED', payload: response.data });
  };
};
