import _ from 'lodash';

export default (state = {}, action) => {
  switch (action.type) {
    case 'ADD_TO_CART_NEW':
      console.log(state);
      return {
        ...state,
        cart: [action.payload],
      };

    case 'ADD_TO_CART_EXISTENT':
      console.log(state);
      return {
        ...state,
        cart: [...action.payload.cart, action.payload.item],
      };

    case 'DELETE_CART_ITEM':
      return {
        ...state,
        cart: action.payload.cart.filter(
          (el) => !_.isEqual(el, action.payload.item)
        ),
      };

    case 'CHECK_IF_ACQUIRED':
      return {
        ...state,
        acquiredModels: action.payload,
      };

    case 'GET_ACQUIRED':
      return {
        ...state,
        acquiredModelsToRender: action.payload,
      };

    default:
      return state;
  }
};
