export default (state = {}, action) => {
  switch (action.type) {
    case 'CREATE_MODEL':
      return { ...state, filename: action.payload };
    case 'IMAGES_TO_UPLOAD':
      return { ...state, images: action.payload };
    case 'IMAGES_TO_SUBMIT':
      return { ...state, imagesSubmit: action.payload };
    case 'FILES_TO_UPLOAD':
      return { ...state, files: action.payload };
    case 'FILES_TO_SUBMIT':
      return { ...state, filesSubmit: action.payload };
    case 'GET_MODELS':
      return { ...state, models: action.payload.models };
    case 'GET_MODEL':
      return { ...state, previewModel: action.payload };
    case 'SELECTED_MODEL':
      return { ...state, selectedModel: action.payload };
    case 'SELECTED_PREVIEW_MODEL':
      return { ...state, selectedPreviewModel: action.payload };
    default:
      return state;
  }
};
