import './AdminForm.css';
import React from 'react';
import { Field } from 'redux-form';

const AdminForm = (props) => {
  return (
    <div className="adminform-container">
      <div>
        <Field name="modelName" component={props.renderInput} />
        <Field name="modelDescription" component={props.renderInput} />
        <div className="three fields">
          <Field name="modelClassification" component={props.renderInput} />
          <Field name="modelFormats" component={props.renderInput} />
          <Field name="modelPrice" component={props.renderInput} />
        </div>
      </div>
      <Field name="modelImages" component={props.renderInput} />
      <Field name="modelFiles" component={props.renderInput} />
      <div>
        <button className="ui submit button">{props.formProps.button1}</button>
      </div>
    </div>
  );
};

export default AdminForm;
