import './AdminWindow.css';
import React from 'react';
import { Link } from 'react-router-dom';
import AddModel from './AddModel/AddModel';
import EditModel from './EditModel/EditModel';
import DeleteModel from './DeleteModel/DeleteModel';
import history from '../../history';


class AdminWindow extends React.Component {

    renderAdmin() {
        const currentAdress = window.location.pathname;

        switch (currentAdress) {
            case '/admin/add-model': 
                return <AddModel />;
            case '/admin/edit-model':
                return <EditModel />;
            case '/admin/delete-model':
                return <DeleteModel />;
            default:
                history.push('/admin/add-model');
                return <AddModel />;
        }
    }
    
    render() {
        return(
            <div className="adminwindow-container">
                <div className="adminwindow-sidebar">
                    <div className="adminwindow-sidebar-header">
                        {/* <img /> */}
                        <div className="adminwindow-sidebar-header-title">Admin - Menu</div>
                    </div>
                    <div className="adminwindow-sidebar-options">
                        <Link to="/admin/add-model" className="adminwindow-sidebar-options-item">Add Model</Link>
                        <Link to="/admin/edit-model" className="adminwindow-sidebar-options-item">Edit Model</Link>
                        <Link to="/admin/delete-model" className="adminwindow-sidebar-options-item">Delete Model</Link>
                    </div>
                </div>
                <div className="adminwindow-content">
                    {this.renderAdmin()}
                </div>
            </div>
        );
    }
}

export default AdminWindow;