import React from 'react';
import { Router, Route, Switch } from 'react-router-dom';
import history from '../history';
import Gallery from './Gallery/Gallery';
import ShoppingCart from './Shop/ShoppingCart/ShoppingCart';
import AdminWindow from './AdminWindow/AdminWindow';
import ModelViewer from './ModelViewer/ModelViewer';
import MyCollection from './Shop/Account/MyCollection/MyCollection';

const App = () => {
  return (
    <div>
      <Router history={history}>
        <Switch>
          <Route path="/admin" component={AdminWindow} />
          <Route path="/" exact component={Gallery} />
          <Route path="/Cart" exact component={ShoppingCart} />
          <Route path="/ModelViewer/:id" exact component={ModelViewer} />
          <Route path="/MyCollection" exact component={MyCollection} />
        </Switch>
      </Router>
    </div>
  );
};

export default App;
